export default {
	data() {
		return {
			cartList: [],
			address: '',
			PriceObj: {
				unitPrice: 0,
				total: 0
			}
		}
	},
	onLoad() {
		this.cartList = uni.getStorageSync("carts").filter(v => v.checked)
		this.address = uni.getStorageSync('address')
		this.cartList.forEach(v => {
			this.PriceObj.unitPrice += v.goods_price * v.num
			this.PriceObj.total += v.num
		})
	},
	methods:{
		toAuth(){
			uni.navigateTo({
				url:"../auth/auth"
			})
		}
	}
}
